import React from 'react';
import { Line } from './utils';
import { Straight, StraightPlus, Broken, BrokenPlus } from './Hexline.css';
interface Props {
  line: Line;
}

const HexLine = ({ line, change }) => {
  console.log(change)
  switch (line) {
    case Line.Broken:
      return <Broken change={change} />
    case Line.BrokenPlus:
      return <BrokenPlus/>
    case Line.Straight:
      return <Straight  change={change}/>
    case Line.StraightPlus:
      return <StraightPlus/>
    default:
      return <p>woops</p>
  }
};

export default HexLine;
